@import "@/assets/global.scss";
















































.themeSelect {
  width: 100%;
  max-width: 366px;
  background: #FFFFFF;
  padding: 3px 15px;
  margin: 16px auto;
  border: 1px solid #FCA311;
  border-radius: 24px;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  > div {
    width: 100%;
    height: 42px;
    padding: 9px 0 9px 9px;
    box-sizing: border-box;
    text-align: left;
  }

  > div:first-child {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
