@import "@/assets/global.scss";














































































































































.main-content {
  padding-top: 254px;
}

.header {
  position: fixed;
  z-index: 10;
  top: 52px;
  width: 100%;
  background-color: #FAFAFA;

  > div {
    width: 100%;
    max-width: $max-width;
    padding-right: 16px;
    padding-left: 16px;
    margin: 0 auto;
  }

  > .bar {
    width: 100%;
    margin: 0 auto;
    border: 1px solid #E9E9E8;
  }
}

.article {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;

  > span {
    display: flex;
    align-items: center;
  }
}

.labelList {
  width: 100%;
  max-width: $max-width;
  margin: 0 auto;
  max-height: 42px;
  overflow-y: hidden;
  box-sizing: border-box;
  transition: max-height 0.5s ease-in-out;
  display: flex;
  flex-wrap: wrap;

  > span {
    @extend .font-14-normal;
    height: 29px;
    background: #FFD5C2;
    border-radius: 100px;
    margin: 6px 4px;
    padding: 4px 8px;
    box-sizing: border-box;
    user-select: none;
  }
}

.expandList {
  max-height: 500px;
}

.link {
  margin-bottom: 32px;
}

.content-panel {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-left: 16px;
  padding-right: 16px;
}
